.CastImg {
  width: 100px;
}
.CastUl {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}
.CastLi {
  text-decoration: none;
  list-style: none;
  width: 200px;
}

.CastWraper {
  margin-top: 10px;
  border: solid 2px rgb(5, 5, 5);
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
